import {t} from 'sn-translate';

import '@servicenow/now-card';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import '@servicenow/now-loader';
import '@servicenow/now-score';
import '@servicenow/now-highlighted-value';

import '../sn-itsm-ettr-card-ml-info';

export default ({
	createdOn,
	originalEstimate,
	estimate,
	state
}) => (
	<now-card className="sn-itsm-ettr-card">
		<h2 className="sn-itsm-ettr-card__header">
			{t('Estimated time to resolve')}
			<now-popover interaction-type="none">
				<now-button-iconic
					slot="trigger"
					icon="circle-info-outline"
					size="md"
					variant="tertiary"
					bare
					hidePadding
					configAria={{
						button:{'aria-label': t('More information')}
					}}
					className="sn-itsm-ettr-card__header__more-info"
				/>
				<sn-itsm-ettr-card-ml-info
					content={t('Generated by ML when the incident was created.')}
					slot="content"
				/>
			</now-popover>
		</h2>
		<now-card-divider blockSpacing="lg" />
		<div className="sn-itsm-ettr-card__body">
			{
				state === 'loading' ?
					<div className="sn-itsm-ettr-card__body__loading">
						<now-loader label="Loading..." size="lg"/>
					</div> : ''
			}
			<p className="sn-itsm-ettr-card__subheader">
				{t('Based on similar incidents')}
			</p>
			<now-score-basic
				score={estimate || '-'}
				hideWhitespace
			/>
			<now-highlighted-value
				label={
					originalEstimate ? 
						t('Original estimate {0}', originalEstimate) :
						t('Unable to predict')
				}
				icon="circle-exclamation-outline"
				color={originalEstimate ? 'info' : 'critical'}
				variant="tertiary"
				className="sn-itsm-ettr-card__original-estimate"
			/>
			<p class={{
				'sn-itsm-ettr-card__created-on': true,
				'sn-itsm-ettr-card__created-on--hide': !createdOn
			}}>
				{t('Created on {0}', createdOn)}
			</p>
		</div>
	</now-card>
);
