import {t} from 'sn-translate';

import {SECONDS_IN_HOUR, SECONDS_IN_DAY} from '../constants';

const hoursUnit = t('hours');
const daysUnit = t('days');

const getIntervalDuration = (lower, upper, units) =>
	t('{0}-{1} {2}', lower === upper ? upper - 1 : lower, upper, units);

export default (lower, upper, maxInDays) => {
	if(upper <= 0)
		return t('0 hours'); 

	const upperDays = Math.round(upper/SECONDS_IN_DAY);
	const lowerDays = Math.max(Math.round(lower/SECONDS_IN_DAY), 1);
	const roundedMax = Math.round(maxInDays);
  
	if (upperDays >= 1 && upper > SECONDS_IN_DAY) {
		if (upperDays >= roundedMax)
			return t('{0}+ days', roundedMax);

		if (upperDays === 1)
			return t('1 day');
		
		return getIntervalDuration(lowerDays, upperDays, daysUnit);
	}
  
	const lowerHours = Math.max(Math.round(lower/SECONDS_IN_HOUR), 1);
	const upperHours = Math.round(upper/SECONDS_IN_HOUR);
	if (upperHours === 0 || upper < SECONDS_IN_HOUR)
		return t('< 1 hour');
  
	if (upperHours === 1)
		return t('1 hour');
  
	return getIntervalDuration(lowerHours, upperHours, hoursUnit);
};
