/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {
	PREDICTION_DATA_FETCHED,
	PREDICTION_DATA_FETCH_FAILED,
	FETCH_DATA
} from './constants';
import {
	handleSuccessfulDataFetch,
	handleFetchError,
	fetchData,
	httpCall,
	destroyTimeout
} from './effects';
import view from './view';
import styles from './_styles.scss';

/**
 * The ITSM ETTR Card displays ML prediction for time to resolve of the incident specified.
 * The component counts down but never reruns prediction even if page or card are refreshed.
 * 
 * ```jsx
 * <sn-itsm-ettr-card
 *		sysid="02591d81539200100999ddeeff7b1270"
 * />
 * ```
 * 
 * @seismicElement sn-itsm-ettr-card
 * @summary A card displaying ML prediction for incident's ETTR.
*/
createCustomElement('sn-itsm-ettr-card', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		/**
		 * `sys_id` of the task for which ETTR prediction should be displayed.
		 * @type {string}
		 * @uib.description sys_id of the task for which ETTR prediction should be displayed.
		 */
		sysid: {
			default: null,
			required: true,
			schema: {
				type: 'string'
			}
		}
	},
	initialState: {
		state: 'loading',
		createdOn: null,
		originalEstimate: null,
		estimate: null,
		timeout: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: fetchData,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: fetchData,
		[FETCH_DATA]: httpCall,
		[PREDICTION_DATA_FETCHED]: handleSuccessfulDataFetch,
		[PREDICTION_DATA_FETCH_FAILED]: handleFetchError,
		[actionTypes.COMPONENT_DISCONNECTED]: destroyTimeout 
	}
});
