import {createHttpEffect} from '@servicenow/ui-effect-http';

import {
	SECONDS_IN_HOUR,
	SECONDS_IN_DAY,
	FETCH_DATA,
	PREDICTION_DATA_FETCHED,
	PREDICTION_DATA_FETCH_FAILED
} from './constants';

import getEstimateDisplayValue from './utils/getEstimateDisplayValue';
import isNumberValid from './utils/isNumberValid';

const setEstimateDisplayTimeout = ({
	lowerBound,
	upperBound,
	upperLimitInDays,
	timeElapsed,
	updateState
}) => {    
	const newLowerBound = lowerBound > 0 ? lowerBound - timeElapsed : 0;
	const newUpperBound = upperBound > 0 ? upperBound - timeElapsed : 0;

	let newTimeElapsed = 0;
  
	const upperDays = Math.round(newUpperBound/SECONDS_IN_DAY);
	if (upperDays >= 1 && newUpperBound > SECONDS_IN_DAY) 
		newTimeElapsed = ((newUpperBound % SECONDS_IN_DAY) || SECONDS_IN_DAY / 2 + 1);
	else 
		newTimeElapsed = ((newUpperBound % SECONDS_IN_HOUR) || SECONDS_IN_HOUR / 2 + 1);

	let newTimeout = null;
	if (newTimeElapsed && newUpperBound && newTimeElapsed > 0) {
		newTimeout = setTimeout(() => {
			setEstimateDisplayTimeout({
				lowerBound: newLowerBound,
				upperBound: newUpperBound,
				upperLimitInDays,
				timeElapsed: newTimeElapsed,
				updateState
			});
		}, newTimeElapsed * 1000);
	}

	updateState({
		timeout: newTimeout,
		estimate: getEstimateDisplayValue(newLowerBound, newUpperBound, upperLimitInDays)
	});
};

export const handleSuccessfulDataFetch = ({
	action: {
		payload: {result}
	},
	updateState,
	state: {
		properties: {sysid: task},
		timeout
	}
}) => {
	updateState({state: 'loaded'});

	if (!result || !result[task] || Object.keys(result[task]).length <= 0 )
		return false;
	
	if (timeout)
		clearTimeout(timeout);

	const {
		lowerbound,
		upperbound,
		created_on: createdOn,
		time_since_creation: timeSinceCreation,
		upper_limit: upperLimitInDays
	} = result[task];

	if (!isNumberValid(lowerbound) || !isNumberValid(upperbound) || !isNumberValid(timeSinceCreation) || !createdOn)
		return false;

	updateState({
		originalEstimate: getEstimateDisplayValue(lowerbound, upperbound, upperLimitInDays),
		createdOn
	});

	setEstimateDisplayTimeout({
		lowerBound: lowerbound - timeSinceCreation,
		upperBound: upperbound - timeSinceCreation,
		upperLimitInDays,
		timeElapsed: 0,
		updateState
	});
};

export const handleFetchError = ({updateState}) => updateState({state: 'loaded'});

export const fetchData = ({
	state: {
		properties: {sysid: task}
	},
	dispatch
}) => dispatch(FETCH_DATA, {incident_sys_id: task});

export const httpCall = createHttpEffect('/api/now/incident_ettr/predict/:incident_sys_id', {
	method: 'POST',
	pathParams: ['incident_sys_id'],
	successActionType: PREDICTION_DATA_FETCHED,
	errorActionType: PREDICTION_DATA_FETCH_FAILED,
	batch: false
});

export const destroyTimeout = ({state: {timeout}}) => timeout && clearTimeout(timeout);
